<template>
    <div class="login-wrapper" v-loading.fullscreen.lock="fullscreenLoading">
        <div class="login-box">
            <h2 class="title"></h2>
            <p class="subtitle"></p>

            <el-form :model="user" label-width="0">
                <!--  -->
                <el-form-item>
                    <el-input prefix-icon="el-icon-user" v-model="user.username" placeholder="">
                    </el-input>
                </el-form-item>

                <!--  -->
                <el-form-item>
                    <el-input prefix-icon="el-icon-lock" type="password" v-model="user.password" placeholder=""
                        show-password @keyup.enter.native="login">
                    </el-input>
                </el-form-item>

                <!--  -->
                <el-form-item>
                    <el-button type="primary" style="width: 100%; margin-bottom: 10px;" @click="login">
                         
                    </el-button>
                    <el-button type="text" style="width: 100%; margin-left: 0;" @click="addUser">
                        
                    </el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
    name: 'userLogin',
    data() {
        return {
            user: {
                username: '',
                password: ''
            },
            fullscreenLoading: false
        }
    },
    methods: {
        login() {
            if (!this.user.username || !this.user.password) {
                this.$message.warning('');
                return;
            }

            this.fullscreenLoading = true;
            // 
            this.$xhr.post('/login', this.user).then(res => {
                if (res.code === 0) {
                    const rawData = res.data;

                    // 1.  ('1':, '2':)
                    const roleId = rawData.charAt(0);

                    // 2.  Token
                    const token = rawData.substring(1);

                    // 3. 
                    sessionStorage.setItem('roleId', roleId);
                    sessionStorage.setItem('token', token);
                    sessionStorage.setItem('username', this.user.username); // 
                    // ============  ============
                    this.$message.success("!");
                    //  Welcome 
                    this.$router.push({ name: 'Welcome' });
                } else {
                    this.$message.error(res.msg || '');
                }
                this.fullscreenLoading = false;
            }).catch(err => {
                console.error(err);
                //  main.js 
                // this.$message.error(""); 
                this.fullscreenLoading = false;
            });
        },
        addUser() {
            this.$router.push({ name: 'NewUser' });
        }
    }
}
</script>

<style scoped>
.login-wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #002140;
    background-size: cover;
    position: relative;
    overflow: hidden;
}

.login-wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('');
    background-size: cover;
    background-position: center;
    opacity: 0.2;
}

.login-box {
    width: 400px;
    padding: 40px 35px;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    position: relative;
    z-index: 2;
}

.title {
    text-align: center;
    margin-bottom: 10px;
    color: #303133;
    font-weight: 700;
    font-size: 32px;
}

.subtitle {
    text-align: center;
    margin-bottom: 30px;
    color: #606266;
    font-size: 16px;
}
</style>