<template>
  <div class="dashboard-container" v-loading="loading">
    <div class="header-banner">
      <div class="header-content">
        <h1></h1>
        <el-button type="primary" size="small" icon="el-icon-refresh" @click="fetchData"></el-button>
      </div>
    </div>

    <!--  KPI  -->
    <el-row :gutter="20" class="mb-20">
      <el-col :span="8" v-for="(item, index) in kpiCards" :key="index">
        <el-card shadow="hover" :body-style="{ padding: '20px' }">
          <div style="display: flex; align-items: center;">
            <div :class="['icon-wrap', 'bg-' + index]">
              <i :class="item.icon"></i>
            </div>
            <div style="margin-left: 15px;">
              <div style="color: #909399; font-size: 14px;">{{ item.label }}</div>
              <div style="font-size: 24px; font-weight: bold; margin-top: 5px;">{{ item.value }}</div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-row :gutter="20">
      <el-col :span="16">
        <el-card shadow="never">
          <div slot="header"><span></span></div>
          <div ref="trendChart" style="height: 350px;"></div>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card shadow="never">
          <div slot="header"><span></span></div>
          <div ref="pieChart" style="height: 350px;"></div>
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-card shadow="never" style="margin-top: 20px;">
      <div slot="header"><span></span></div>
      <el-table :data="tableData" style="width: 100%" height="300">
        <el-table-column prop="proName" label=""></el-table-column>
        <el-table-column prop="category" label="" width="120">
          <template slot-scope="scope"><el-tag size="small">{{ scope.row.category }}</el-tag></template>
        </el-table-column>
        <el-table-column prop="salesCount" label="" sortable></el-table-column>
        <el-table-column prop="salesAmount" label="()" sortable></el-table-column>
        <el-table-column prop="lastSaleTime" label=""></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'DashboardView',
  data() {
    return {
      loading: false,
      kpiCards: [
        { label: '', value: 0, icon: 'el-icon-s-order' },
        { label: '', value: 0, icon: 'el-icon-money' },
        { label: '', value: 0, icon: 'el-icon-wallet' }
      ],
      tableData: [],
      charts: { trend: null, pie: null }
    }
  },
  mounted() {
    this.charts.trend = echarts.init(this.$refs.trendChart);
    this.charts.pie = echarts.init(this.$refs.pieChart);
    this.fetchData();
    window.addEventListener('resize', this.handleResize);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    async fetchData() {
      this.loading = true;
      try {
        const res = await this.$xhr.post('/api/dashboard/data', {});
        console.log('Dashboard Data:', res);

        // res 
        //  res  kpi  trends
        if (res && (res.kpi || res.trends || res.tableData)) {

          //  res  data 
          const data = res;

          // 1.  KPI ()
          if (data.kpi) {
            this.kpiCards[0].value = data.kpi.totalCount || 0;
            this.kpiCards[1].value = data.kpi.totalAmount || 0;
            this.kpiCards[2].value = data.kpi.totalProfit || 0;

            // 
            if (data.kpi.details && data.kpi.details.length > 0) {
              this.updatePieChart(data.kpi.details[0]);
            }
          }

          // 2. 
          this.tableData = data.tableData || [];

          // 3. 
          if (data.trends) {
            this.updateTrendChart(data.trends);
          }
        }
        //  res  {code:0, data:{...}}
        else if (res && res.code === 0 && res.data) {
          const data = res.data;
          // ...  renderData 
          if (data.kpi) {
            this.kpiCards[0].value = data.kpi.totalCount || 0;
            this.kpiCards[1].value = data.kpi.totalAmount || 0;
            this.kpiCards[2].value = data.kpi.totalProfit || 0;
            if (data.kpi.details && data.kpi.details.length > 0) this.updatePieChart(data.kpi.details[0]);
          }
          this.tableData = data.tableData || [];
          if (data.trends) this.updateTrendChart(data.trends);
        }
        else {
          console.warn("");
        }
      } catch (e) {
        console.error("", e);
      } finally {
        this.loading = false;
      }
    },
    updateTrendChart(trends) {
      const option = {
        tooltip: { trigger: 'axis' },
        legend: { data: ['', ''] },
        xAxis: { type: 'category', data: trends.years }, //  API  key  years 
        yAxis: { type: 'value' },
        series: [
          { name: '', type: 'line', data: trends.amount, smooth: true, itemStyle: { color: '#409EFF' } },
          { name: '', type: 'line', data: trends.profit, smooth: true, itemStyle: { color: '#67C23A' } }
        ]
      };
      this.charts.trend.setOption(option);
    },
    updatePieChart(data) {
      const option = {
        tooltip: { trigger: 'item' },
        series: [{
          type: 'pie',
          radius: ['40%', '70%'],
          data: data, // {value, name} 
          itemStyle: { borderRadius: 10, borderColor: '#fff', borderWidth: 2 }
        }]
      };
      this.charts.pie.setOption(option);
    },
    handleResize() {
      this.charts.trend && this.charts.trend.resize();
      this.charts.pie && this.charts.pie.resize();
    }
  }
}
</script>

<style scoped>
.dashboard-container {
  padding: 0;
}

.header-banner {
  background: #fff;
  padding: 15px 20px;
  margin-bottom: 20px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-content h1 {
  margin: 0;
  font-size: 20px;
  color: #303133;
}

.icon-wrap {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 24px;
}

.bg-0 {
  background: #409EFF;
}

.bg-1 {
  background: #36D1DC;
}

.bg-2 {
  background: #F56C6C;
}

.mb-20 {
  margin-bottom: 20px;
}
</style>