<template>
  <div class="app-container">
    <el-card shadow="never">
      <!--  -->
      <div slot="header" style="display: flex; justify-content: space-between; align-items: center;">
        <div style="font-weight: bold; font-size: 16px;">
            <i class="el-icon-tickets"></i> 
        </div>
        <div>
           <el-input 
             v-model="search" 
             placeholder="/" 
             style="width: 250px; margin-right: 10px;" 
             size="small"
             clearable>
           </el-input>
           <el-button type="primary" icon="el-icon-refresh" size="small" @click="fetchData"></el-button>
        </div>
      </div>

      <!--  -->
      <el-table :data="filteredData" v-loading="loading" border style="width: 100%" stripe>
        
        <el-table-column type="index" label="" width="60" align="center"></el-table-column>
        
        <el-table-column prop="modifyTime" label="" width="180" sortable>
            <template slot-scope="scope">
                <i class="el-icon-time"></i> {{ scope.row.modifyTime.replace('T', ' ') }}
            </template>
        </el-table-column>

        <el-table-column prop="product" label="" min-width="150" sortable></el-table-column>

        <el-table-column prop="changeType" label="" width="100" align="center">
            <template slot-scope="scope">
                <el-tag 
                    effect="dark"
                    :type="['', '', '', '', ''].includes(scope.row.changeType) ? 'success' : 'danger'">
                    {{ scope.row.changeType }}
                </el-tag>
            </template>
        </el-table-column>

        <el-table-column prop="quantity" label="" width="120" align="right">
            <template slot-scope="scope">
                <span :style="{
                    color: scope.row.changeType === '' ? '#67C23A' : '#F56C6C', 
                    fontWeight: 'bold',
                    fontSize: '15px'
                }">
                    {{ scope.row.changeType === '' ? '+' : '-' }}{{ scope.row.quantity }}
                </span>
            </template>
        </el-table-column>

        <el-table-column prop="operator" label="" width="120" align="center">
            <template slot-scope="scope">
                <el-tag size="small" type="info">{{ scope.row.operator }}</el-tag>
            </template>
        </el-table-column>

        <el-table-column prop="note" label="/" min-width="200" show-overflow-tooltip></el-table-column>

      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'InventoryLogPage',
  data() {
    return {
      loading: false,
      tableData: [],
      search: ''
    };
  },
  computed: {
    // 
    filteredData() {
        if (!this.search) return this.tableData;
        const key = this.search.toLowerCase();
        return this.tableData.filter(item => 
            (item.product && item.product.toLowerCase().includes(key)) ||
            (item.operator && item.operator.toLowerCase().includes(key)) ||
            (item.note && item.note.toLowerCase().includes(key))
        );
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      this.loading = true;
      try {
        const res = await this.$xhr.get('/inventoryLog/list');
        if (res.code === 0) {
          this.tableData = res.data;
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>