<template>
    <div class="register-container">
        <el-card class="register-box">
            <div slot="header" class="register-header">
                <span class="header-title"></span>
                <el-button style="float: right; padding: 3px 0" type="text"
                    @click="$router.push('/login')"></el-button>
            </div>

            <!-- model  formrules  -->
            <el-form :model="form" status-icon :rules="rules" ref="form" label-width="80px">
                <el-form-item label="" prop="userName">
                    <el-input v-model="form.userName" placeholder=""></el-input>
                </el-form-item>

                <!--  -->
                <el-form-item label="" prop="password">
                    <el-input type="password" v-model="form.password" autocomplete="off" placeholder=""></el-input>
                </el-form-item>

                <!--  -->
                <el-form-item label="" prop="checkPass">
                    <el-input type="password" v-model="form.checkPass" autocomplete="off"
                        placeholder=""></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="register" style="width: 100%; margin-bottom: 10px;" :loading="loading"></el-button>
                    <el-button @click="resetForm" style="width: 100%; margin-left: 0;"></el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
export default {
    name: 'NewUser',
    data() {
        // 1. 
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                // 
                if (this.form.checkPass !== '') {
                    this.$refs.form.validateField('checkPass');
                }
                callback();
            }
        };

        // 2. 
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.form.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };

        return {
            loading: false,
            form: {
                userId: '',
                userName: '',
                password: '',
                checkPass: '', // 
                userRole: '', // 
                userStatus: ''
            },
            rules: {
                userName: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                // trigger: 'change' 
                password: [
                    { validator: validatePass, trigger: 'change' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'change' }
                ]
            }
        }
    },
    methods: {
        register() {
            this.$refs.form.validate(valid => {
                if (valid) {
                    this.loading = true;

                    //   ESLint no-unused-vars
                    // 1.  form 
                    const submitData = { ...this.form };
                    // 2.  checkPass 
                    delete submitData.checkPass;

                    this.$xhr.post('/user/addUser', submitData).then(res => {
                        if (res.code === 0) {
                            this.$message.success('');
                            this.$router.push('/login');
                        } else {
                            this.$message.error(res.msg || '');
                        }
                    }).catch(e => {
                        console.error(e);
                        this.$message.error("");
                    }).finally(() => {
                        this.loading = false;
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        resetForm() {
            this.$refs.form.resetFields();
        }
    }
}
</script>

<style scoped>
.register-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #002140;
    background-size: cover;
    position: relative;
    overflow: hidden;
}

.register-container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('');
    background-size: cover;
    background-position: center;
    opacity: 0.2;
}

.register-box {
    width: 400px;
    padding: 40px 35px;
    border-radius: 10px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    background: rgba(255, 255, 255, 0.9);
    position: relative;
    z-index: 2;
}

.register-header {
    text-align: center;
}

.header-title {
    font-weight: 700;
    font-size: 24px;
    color: #303133;
}

.el-form {
    margin-top: 20px;
}

.el-form-item {
    margin-bottom: 20px;
}

.el-form-item__label {
    font-weight: 500;
    color: #303133;
}

.el-form-item__content {
    display: flex;
    justify-content: center;
}

.el-input {
    width: 100%;
}

.el-button {
    width: 100%;
    margin-bottom: 10px;
}

/*  */
.el-form-item .el-button {
    display: block;
    margin: 0 auto;
}
</style>