<template>
  <div class="app-container">
    <el-card class="box-card" shadow="never">
      <!--  -->
      <div slot="header" style="display: flex; justify-content: space-between;">
        <div style="font-weight: bold; font-size: 16px;">
          <i class="el-icon-shopping-cart-2"></i> 
        </div>
        <div>
          <el-button icon="el-icon-refresh" size="small" @click="fetchData"></el-button>
        </div>
      </div>

      <el-row :gutter="20">
        <!--  -->
        <el-col :span="16">
          <el-card shadow="never">
            <div slot="header"></div>
            <el-input
              v-model="search"
              placeholder=""
              size="small"
              style="width: 200px; margin-bottom: 15px;"
              clearable
            ></el-input>
            
            <el-table 
              :data="filteredData" 
              v-loading="loading" 
              border 
              style="width: 100%"
              highlight-current-row
              @current-change="handleSelectProduct"
            >
              <el-table-column prop="proId" label="ID" width="60" align="center"></el-table-column>
              <el-table-column prop="proName" label="" min-width="150"></el-table-column>
              <el-table-column prop="proCategory" label="" width="100" align="center">
                <template slot-scope="scope">
                  <el-tag size="small" type="info">{{ scope.row.proCategory }}</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="proPrice" label="" width="100" sortable align="right">
                <template slot-scope="scope"> {{ scope.row.proPrice }}</template>
              </el-table-column>
              
              <el-table-column prop="proNum" label="" width="100" sortable align="center">
                   <template slot-scope="scope">
                      <div v-if="scope.row.proNum <= 10" style="color: red; font-weight: bold;">
                          <i class="el-icon-warning"></i> {{ scope.row.proNum }}
                      </div>
                      <div v-else style="color: #67C23A; font-weight: bold;">
                          {{ scope.row.proNum }}
                      </div>
                   </template>
              </el-table-column>
            </el-table>
          </el-card>
        </el-col>
        
        <!--  -->
        <el-col :span="8">
          <el-card shadow="never" style="height: 100%;">
            <div slot="header"></div>
            
            <el-form :model="saleForm" label-width="80px" ref="saleForm" :rules="rules">
              <el-form-item label="" prop="proName">
                <el-input v-model="saleForm.proName" disabled></el-input>
              </el-form-item>
              
              <el-form-item label="" prop="unitPrice">
                <el-input v-model.number="saleForm.unitPrice" disabled>
                  <template slot="append"></template>
                </el-input>
              </el-form-item>
              
              <el-form-item label="" prop="stock">
                <el-input v-model.number="saleForm.stock" disabled></el-input>
              </el-form-item>
              
              <el-form-item label="" prop="quantity">
                <el-input-number 
                  v-model="saleForm.quantity" 
                  :min="1" 
                  :max="saleForm.stock" 
                  style="width: 100%"
                  @change="calculateTotal"
                ></el-input-number>
              </el-form-item>
              
              <el-form-item label="" prop="totalPrice">
                <el-input v-model.number="saleForm.totalPrice" disabled>
                  <template slot="append"></template>
                </el-input>
              </el-form-item>
              
              <el-form-item label="" prop="customerName">
                <el-input v-model="saleForm.customerName" placeholder=""></el-input>
              </el-form-item>
              
              <el-form-item label="" prop="remark">
                <el-input 
                  v-model="saleForm.remark" 
                  type="textarea" 
                  placeholder=""
                  :rows="3"
                ></el-input>
              </el-form-item>
              
              <el-form-item>
                <el-button 
                  type="primary" 
                  style="width: 100%" 
                  @click="submitSale" 
                  :disabled="!saleForm.proId || submitting"
                  :loading="submitting"
                >
                  
                </el-button>
                
                <el-button 
                  style="width: 100%; margin-top: 10px;" 
                  @click="resetForm"
                >
                  
                </el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
    
    <!--  -->
    <el-card shadow="never" style="margin-top: 20px;">
      <div slot="header" style="display: flex; justify-content: space-between;">
        <div></div>
        <div>
          <el-date-picker
            v-model="dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            size="small"
            style="margin-right: 10px;"
            value-format="yyyy-MM-dd"
            @change="fetchSalesRecords"
          ></el-date-picker>
          <el-button icon="el-icon-search" size="small" @click="fetchSalesRecords"></el-button>
        </div>
      </div>
      
      <el-table :data="salesRecords" v-loading="recordsLoading" border style="width: 100%">
        <el-table-column prop="saleId" label="" width="100"></el-table-column>
        <el-table-column prop="productName" label="" min-width="150"></el-table-column>
        <el-table-column prop="quantity" label="" width="100" align="center"></el-table-column>
        <el-table-column prop="unitPrice" label="" width="100" align="right">
          <template slot-scope="scope"> {{ scope.row.unitPrice }}</template>
        </el-table-column>
        <el-table-column prop="totalPrice" label="" width="100" align="right">
          <template slot-scope="scope"> {{ scope.row.totalPrice }}</template>
        </el-table-column>
        <el-table-column prop="customerName" label="" width="120"></el-table-column>
        <el-table-column prop="saleTime" label="" width="180"></el-table-column>
        <el-table-column prop="remark" label="" min-width="150" show-overflow-tooltip></el-table-column>
      </el-table>
      
      <div style="text-align: right; margin-top: 15px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalRecords"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'SalesManagerPage',
  data() {
    return {
      loading: false,
      recordsLoading: false,
      submitting: false,
      tableData: [],
      search: '',
      saleForm: {
        proId: null,
        proName: '',
        unitPrice: 0,
        stock: 0,
        quantity: 1,
        totalPrice: 0,
        customerName: '',
        remark: ''
      },
      salesRecords: [],
      dateRange: [],
      currentPage: 1,
      pageSize: 10,
      totalRecords: 0,
      
      rules: {
        quantity: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 1, message: '0', trigger: 'change' },
          { 
            validator: (rule, value, callback) => {
              if (value > this.saleForm.stock) {
                callback(new Error(''));
              } else {
                callback();
              }
            }, 
            trigger: 'change' 
          }
        ],
        customerName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  computed: {
    filteredData() {
      if (!this.search) return this.tableData;
      return this.tableData.filter(data => 
        !this.search || data.proName.toLowerCase().includes(this.search.toLowerCase())
      );
    }
  },
  created() {
    this.fetchData();
    this.fetchSalesRecords();
  },
  methods: {
    async fetchData() {
      this.loading = true;
      try {
        const res = await this.$xhr.get('/product/list');
        if (res.code === 0) {
          this.tableData = res.data;
        }
      } catch (e) { 
        console.error(e); 
      } finally { 
        this.loading = false; 
      }
    },
    
    handleSelectProduct(row) {
      if (row) {
        this.saleForm.proId = row.proId;
        this.saleForm.proName = row.proName;
        this.saleForm.unitPrice = row.proPrice;
        this.saleForm.stock = row.proNum;
        this.calculateTotal();
      }
    },
    
    calculateTotal() {
      this.saleForm.totalPrice = parseFloat((this.saleForm.unitPrice * this.saleForm.quantity).toFixed(2));
    },
    
    resetForm() {
      this.saleForm = {
        proId: null,
        proName: '',
        unitPrice: 0,
        stock: 0,
        quantity: 1,
        totalPrice: 0,
        customerName: '',
        remark: ''
      };
      this.$refs.saleForm.resetFields();
    },
    
    async submitSale() {
      this.$refs.saleForm.validate(async (valid) => {
        if (valid) {
          this.submitting = true;
          
          // 
          const saleData = {
            proId: this.saleForm.proId,
            quantity: this.saleForm.quantity,
            unitPrice: this.saleForm.unitPrice,
            totalPrice: this.saleForm.totalPrice,
            customerName: this.saleForm.customerName,
            remark: this.saleForm.remark,
            operator: sessionStorage.getItem('username') || ''
          };
          
          try {
            const res = await this.$xhr.post('/sales/add', saleData);
            if (res.code === 0) {
              this.$message.success('');
              this.resetForm();
              this.fetchData(); // 
              this.fetchSalesRecords(); // 
            } else {
              this.$message.error(res.msg || '');
            }
          } catch (e) {
            console.error(e);
            this.$message.error('');
          } finally {
            this.submitting = false;
          }
        }
      });
    },
    
    async fetchSalesRecords() {
      this.recordsLoading = true;
      try {
        const params = {
          page: this.currentPage,
          size: this.pageSize
        };
        
        if (this.dateRange && this.dateRange.length === 2) {
          params.startDate = this.dateRange[0];
          params.endDate = this.dateRange[1];
        }
        
        const res = await this.$xhr.get('/sales/list', { params });
        if (res.code === 0) {
          this.salesRecords = res.data.records;
          this.totalRecords = res.data.total;
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.recordsLoading = false;
      }
    },
    
    handleSizeChange(val) {
      this.pageSize = val;
      this.currentPage = 1;
      this.fetchSalesRecords();
    },
    
    handleCurrentChange(val) {
      this.currentPage = val;
      this.fetchSalesRecords();
    }
  }
};
</script>

<style scoped>
.app-container {
  padding: 20px;
}
</style>