<template>
  <div class="app-container">
    <el-card shadow="never">
      <div slot="header">
        <el-button type="primary" icon="el-icon-plus" @click="handleAdd"></el-button>
        <el-button icon="el-icon-refresh" @click="fetchData"></el-button>
      </div>

      <el-table :data="tableData" v-loading="loading" border>
        <el-table-column prop="userId" label="ID/" width="150"></el-table-column>
        <el-table-column prop="userName" label=""></el-table-column>
        <el-table-column prop="userRole" label="">
           <template slot-scope="scope">
               <el-tag>{{ scope.row.userRole }}</el-tag>
           </template>
        </el-table-column>
        <el-table-column prop="userStatus" label=""></el-table-column>
        <el-table-column prop="creationTime" label="" width="180"></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-dialog :title="form.isEdit ? '' : ''" :visible.sync="dialogVisible" width="500px">
      <el-form :model="form" label-width="80px">
        <el-form-item label="ID">
          <el-input v-model="form.userId" :disabled="form.isEdit" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.userName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="form.password" type="password" placeholder="" v-if="form.isEdit"></el-input>
            <el-input v-model="form.password" type="password" placeholder="" v-else></el-input>
        </el-form-item>
        <el-form-item label="">
           <el-select v-model="form.userRole" style="width:100%">
               <el-option value="" label=""></el-option>
               <el-option value="" label=""></el-option>
           </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="form.userStatus" style="width:100%">
                <el-option value="" label=""></el-option>
                <el-option value="" label=""></el-option>
            </el-select>
         </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'UserManagerPage', //  ESLint 
  data() {
    return {
      loading: false,
      tableData: [],
      dialogVisible: false,
      form: { userId: '', userName: '', password: '', userRole: '', userStatus: '', isEdit: false }
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      this.loading = true;
      try {
        const res = await this.$xhr.get('/user/all');
        if (res.code === 0) {
          this.tableData = res.data;
        }
      } catch (e) { console.error(e) }
      finally { this.loading = false; }
    },
    handleAdd() {
      this.form = { userId: '', userName: '', password: '', userRole: '', userStatus: '', isEdit: false };
      this.dialogVisible = true;
    },
    handleEdit(row) {
      // 
      this.form = { ...row, password: '', isEdit: true };
      this.dialogVisible = true;
    },
    async handleDelete(row) {
      try {
        await this.$confirm('?', '', { type: 'warning' });
        await this.$xhr.post('/user/deleteUser', row);
        this.$message.success('');
        this.fetchData();
      } catch (e) { /* cancel */ }
    },
    async submitForm() {
      //  isEdit
      const submitData = { ...this.form };
      delete submitData.isEdit;
      
      const url = this.form.isEdit ? '/user/update' : '/user/addUser';
      try {
        await this.$xhr.post(url, submitData);
        this.$message.success('');
        this.dialogVisible = false;
        this.fetchData();
      } catch (e) { console.error(e) }
    }
  }
};
</script>