import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login.vue'
import Welcome from '@/views/Welcome.vue' // 
import Dashboard from '@/views/Dashboard.vue'
import ProductManager from '@/views/ProductManager.vue'
import DocumentManager from '@/views/DocumentManager.vue'
import LocationManager from '@/views/LocationManager.vue'
import UserManager from '@/views/UserManager.vue'
import NewUser from '@/views/NewUser.vue'
import InventoryLog from '@/views/InventoryLog.vue'
import SalesManager from '@/views/SalesManager.vue'

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: '/login',
            name: 'Login',
            component: Login,
        },
        {
            path: '/newUser',
            name: 'NewUser',
            component: NewUser
        },
        {
            path: '/',
            redirect: '/login'
        },
        {
            // 
            path: '/welcome',
            name: 'Welcome',
            component: Welcome,
            redirect: '/welcome/dashboard', // 
            children: [
                {
                    path: 'dashboard',
                    name: 'Dashboard',
                    component: Dashboard,
                    meta: { title: '' }
                },
                {
                    path: 'products',
                    name: 'ProductManager',
                    component: ProductManager,
                    meta: { title: '' }
                },
                {
                    path: 'documents',
                    name: 'DocumentManager',
                    component: DocumentManager,
                    meta: { title: '' }
                },
                {
                    path: 'locations',
                    name: 'LocationManager',
                    component: LocationManager,
                    meta: { title: '' }
                },
                {
                    path: 'users',
                    name: 'UserManager',
                    component: UserManager,
                    meta: { title: '' }
                },
                {
                    path: 'logs',
                    name: 'InventoryLog',
                    component: InventoryLog,
                    meta: { title: '' }
                },
                {
                    path: 'sales',
                    name: 'SalesManager',
                    component: SalesManager,
                    meta: { title: '' }
                }
            ]
        }
    ]
});

// 
router.beforeEach((to, from, next) => {
    const token = sessionStorage.getItem("token");
    const roleId = sessionStorage.getItem("roleId"); // 

    // 1. 
    if (to.path !== '/login' && to.path !== '/newUser' && !token) {
        next('/login');
    }
    // 2. (roleId != 1)   
    else if (to.path.includes('/users') && roleId !== '1') {
        Vue.prototype.$message.warning('');
        next('/welcome/dashboard'); // 
    }
    // 3. 
    else {
        next();
    }
});

export default router;