<template>
    <div class="app-container">
        <el-card shadow="never">
            <!--  -->
            <div slot="header" style="display: flex; justify-content: space-between;">
                <div>
                    <el-input v-model="searchKeyword" placeholder="/" style="width: 200px; margin-right: 10px;"
                        size="small"></el-input>
                    <el-button type="primary" icon="el-icon-search" size="small" @click="fetchData"></el-button>
                </div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="preAdd"></el-button>
            </div>

            <!--  -->
            <el-table :data="filteredData" v-loading="loading" border>
                <el-table-column prop="docId" label="" width="100"></el-table-column>
                <el-table-column prop="docType" label="" width="120">
                    <template slot-scope="scope">
                        <el-tag
                            :type="['', '', '', '', ''].includes(scope.row.docType) ? 'success' : 'warning'"
                            effect="dark">{{
                                scope.row.docType }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" min-width="140">
                    <template slot-scope="scope">
                        <div>{{ getProductName(scope.row.proId) }}</div>
                        <div style="color: #999; font-size: 12px;">: {{ scope.row.docCount }}</div>
                    </template>
                </el-table-column>
                <el-table-column prop="docStatus" label="" width="100">
                    <template slot-scope="scope">
                        <span
                            :style="{ color: scope.row.docStatus === '' ? '#67C23A' : '#F56C6C', fontWeight: 'bold' }">
                            {{ scope.row.docStatus }}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column prop="transactionType" label="" width="120"></el-table-column>
                <el-table-column prop="creationTime" label="" width="160"></el-table-column>
                <el-table-column label="" prop="docHandler"></el-table-column>
                <el-table-column label="" width="220" fixed="right" align="center">
                    <template slot-scope="scope">
                        <div style="display: flex; justify-content: center; align-items: center;">

                            <!-- 1.  () -->
                            <el-button v-if="scope.row.docStatus !== '' && isAdmin" type="text" size="small"
                                style="color: #67C23A; font-weight: bold;" icon="el-icon-check"
                                @click="handleApprove(scope.row)">
                                
                            </el-button>

                            <el-divider direction="vertical"
                                v-if="scope.row.docStatus !== '' && isAdmin"></el-divider>

                            <!-- 2.  () -->
                            <el-button type="text" size="small" icon="el-icon-view" @click="preView(scope.row)">
                                
                            </el-button>

                            <!--  -->
                            <el-divider direction="vertical"
                                v-if="scope.row.docStatus !== '' || isAdmin"></el-divider>

                            <!-- 3.  ( OR ) -->
                            <el-button v-if="scope.row.docStatus !== '' || isAdmin" type="text" size="small"
                                style="color: #F56C6C;" icon="el-icon-delete" @click="deleteDocument(scope.row)">
                                
                            </el-button>

                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <!-- / -->
        <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" width="600px" :close-on-click-modal="false">
            <el-form :model="form" label-width="100px" :rules="rules" ref="docForm" :disabled="isViewMode">
                <el-form-item label="" prop="docType">
                    <el-radio-group v-model="form.docType">
                        <el-radio label="" border></el-radio>
                        <el-radio label="" border></el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="" prop="proId">
                    <el-select v-model="form.proId" placeholder="" style="width: 100%" filterable>
                        <el-option v-for="item in productList" :key="item.proId" :label="item.proName"
                            :value="item.proId">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="docCount">
                    <el-input-number v-model="form.docCount" :min="1" style="width: 100%"></el-input-number>
                </el-form-item>

                <el-form-item label="" prop="docType">
                    <el-select v-model="form.docType" placeholder="" style="width: 100%">
                        <el-option v-for="item in transactionTypes" :key="item.value" :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="docHandler">
                    <el-input v-model="form.docHandler" :disabled="true"></el-input>
                </el-form-item>

                <!--  () -->
                <el-form-item label="">
                    <!--  action  -->
                    <el-upload :action="uploadUrl" list-type="picture-card" :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove" :headers="headers" :on-success="uploadSuccess" :file-list="fileList"
                        :disabled="isViewMode || (!isAdmin && form.docStatus === '')">
                        <i class="el-icon-plus"></i>
                        <!--   slot="file"  -->
                        <div slot="file" slot-scope="{file}" style="width: 100%; height: 100%;">
                            <img class="el-upload-list__item-thumbnail" :src="file.url" alt=""
                                style="width: 100%; height: 100%; object-fit: cover;">
                            <span class="el-upload-list__item-actions">
                                <!--  -->
                                <span class="el-upload-list__item-preview" @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>

                                <!--  () -->
                                <span class="el-upload-list__item-delete" v-if="form.docStatus !== ''"
                                    @click="handleRemove(file, fileList)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                    <!--  -->
                    <el-dialog :visible.sync="picDialogVisible" :modal="false">
                        <img width="100%" :src="dialogImageUrl" alt="" />
                    </el-dialog>
                </el-form-item>

            </el-form>
            <!--   -->
            <div slot="footer" class="dialog-footer">

                <!-- 1 -->
                <!-- """" -->
                <el-button @click="dialogFormVisible = false">
                    {{ isViewMode ? ' ' : ' ' }}
                </el-button>

                <!-- 2"""" -->
                <el-button v-if="!isViewMode" type="primary" :loading="submitting" @click="addOrUpdate">
                     
                </el-button>

            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            loading: false,
            searchKeyword: '',
            tableData: [],
            productList: [],
            currentUserRole: '2',
            currentUsername: '',
            submitting: false,
            // 
            dialogFormVisible: false,
            dialogTitle: '',
            isViewMode: false, // ()

            // 
            form: {
                docType: '',
                docHandler: '',
                docStatus: '',
                picIds: [],
                proId: null,
                docCount: 1
            },

            // 
            transactionTypes: [
                { value: '', label: '' },
                { value: '', label: '' },
                { value: '', label: '' },
                { value: '', label: '' },
                { value: '', label: '' },
                { value: '', label: '' },
                { value: '', label: '' },
                { value: '', label: '' }
            ],

            // 
            fileList: [],
            dialogImageUrl: '',
            picDialogVisible: false,
            // 
            uploadUrl: process.env.VUE_APP_API_BASE_URL + '/docPic/save',
            headers: {
                Authorization: `Bearer ${this.getToken()}`
            },

            rules: {
                docType: [{ required: true, message: '', trigger: 'change' }],
                proId: [{ required: true, message: '', trigger: 'change' }],
                docCount: [{ required: true, message: '', trigger: 'blur' }],
                docHandler: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    computed: {
        filteredData() {
            // 1. 
            let data = this.tableData;

            // 2. ('2') ''   
            if (this.currentUserRole === '2') {
                data = data.filter(item => item.docStatus === '' || item.docHandler === this.currentUsername);
            }
            // 3.  ()
            if (this.searchKeyword) {
                const keyword = this.searchKeyword.toLowerCase();
                data = data.filter(item =>
                    (item.docHandler && item.docHandler.toLowerCase().includes(keyword)) ||
                    (item.docId && String(item.docId).includes(keyword))
                );
            }

            return data;
        },
        isAdmin() {
            return this.currentUserRole === '1';
        }
    },
    created() {
        // 
        this.currentUserRole = sessionStorage.getItem('roleId') || '2';
        this.currentUsername = sessionStorage.getItem('username') || '';
        this.fetchData();
        this.fetchProducts();
    },
    methods: {
        getToken() {
            return sessionStorage.getItem('token');
        },
        async fetchData() {
            this.loading = true;
            const res = await this.$xhr.get('/document/list');
            if (res.code === 0) this.tableData = res.data;
            this.loading = false;
        },
        async fetchProducts() {
            const res = await this.$xhr.get('/product/list');
            if (res.code === 0) this.productList = res.data;
        },
        getProductName(id) {
            const p = this.productList.find(item => item.proId === id);
            return p ? p.proName : '';
        },

        // ---  ---

        // 1. ""
        preAdd() {
            this.dialogTitle = '';
            this.isViewMode = false;
            this.fileList = []; // 
            const currentUser = sessionStorage.getItem('username') || '';
            this.form = {
                docType: '',
                docHandler: currentUser,
                docStatus: '',
                picIds: [],
                proId: null,
                docCount: 1
            };
            this.dialogFormVisible = true;
            this.$nextTick(() => { this.$refs.docForm && this.$refs.docForm.clearValidate(); });
        },

        // 2.  ()
        addOrUpdate() {
            this.$refs.docForm.validate(async valid => {
                if (valid) {
                    this.submitting = true;
                    try {
                        //   docId    
                        //  form.docId  /update 
                        //  /add 
                        const url = this.form.docId ? '/document/update' : '/document/add';

                        const res = await this.$xhr.post(url, this.form);

                        if (res.code === 0) {
                            this.$message.success(this.form.docId ? '' : '');
                            this.dialogFormVisible = false;
                            this.fetchData();
                        } else {
                            this.$message.error(res.msg);
                        }
                    } catch (e) {
                        console.error(e);
                        this.$message.error("");
                    } finally {
                        this.submitting = false;
                    }
                }
            })
        },

        // 3. "" ( preUpdate )
        preView(row) {
            //   ''  
            if (this.currentUserRole === '2' && row.docStatus === '') {
                this.isViewMode = true;
                if (row.docHandler !== this.currentUsername) {
                    this.$message.warning('');
                    return;
                }
                
            } else {
                if (row.docStatus === '') {
                    this.isViewMode = true;
                } else {
                    this.isViewMode = false;
                }
            }

            // """"
            this.dialogTitle = this.isViewMode ? '' : '';

            this.form = { ...row }; // 
            this.fileList = [];

            if (!this.form.picIds) this.form.picIds = [];

            // 
            this.$xhr.get(`/docPic/getPics/${row.docId}`).then(res => {
                if (res.code === 0) {
                    const list = res.data;
                    list.forEach(pic => {
                        const fullUrl = pic.picPath.startsWith('http') ? pic.picPath : (process.env.VUE_APP_API_BASE_URL + pic.picPath);
                        this.fileList.push({
                            name: pic.picPath,
                            url: fullUrl,
                            response: { data: { docPicId: pic.docPicId } },
                            rawId: pic.docPicId
                        });
                    });
                }
                this.dialogFormVisible = true;
            });
        },

        // 
        uploadSuccess(res) {
            // res  /docPic/save  Response<DocPicInfo>
            if (res.code === 0) {
                if (!this.form.picIds) this.form.picIds = [];
                // ID
                this.form.picIds.push(res.data.docPicId);
            } else {
                this.$message.error(': ' + res.msg);
            }
        },
        deletePic(file) {
            this.$xhr.post('/docPic/deletePic', { url: file.url }).then(res => {
                if (res.code == 0) {
                    this.$message.success("");
                    this.fetchData();
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        // 
        handleRemove(file, fileList) {
            this.fileList = [...fileList];
            //
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                // 
                this.deletePic(file);
                this.fileList = this.fileList.filter(item => item.rawId !== file.rawId);
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
                //
                this.fileList.push(file);
            });
        },

        // 
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.picDialogVisible = true;
        },

        // ---  ---

        async handleApprove(row) {
            try {
                await this.$confirm(`\n`, '', { type: 'warning' });
                const res = await this.$xhr.post('/document/approve', { docId: row.docId, operator: '' });
                if (res.code === 0) {
                    this.$message.success('');
                    this.fetchData();
                    // 
                    this.$root.$emit('documentApproved');
                } else {
                    this.$message.error(res.msg || '');
                }
            } catch (e) {
                console.error(e);
                this.$message.error('');
            }
        },
        async deleteDocument(row) {
            try {
                let tipMessage = '?';

                // 
                if (row.docStatus === '') {
                    tipMessage = ' \n\n';
                }

                await this.$confirm(tipMessage, '', {
                    type: 'warning',
                    confirmButtonText: '',
                    cancelButtonText: ''
                });

                await this.$xhr.post('/document/delete', row);
                this.$message.success('');
                this.fetchData();
                // 
                if (row.docStatus === '') {
                    this.$root.$emit('documentApproved');
                }
            } catch (e) {
                console.log("");
            }
        }
    }
}
</script>