<template>
  <el-container style="height: 100vh;">
    <!--  -->
    <el-aside width="220px" style="background-color: #001529; overflow-x: hidden;">
      <div class="sys-logo">WMS</div>
      <el-menu :default-active="$route.path" router background-color="#001529" text-color="rgba(255,255,255,0.7)"
        active-text-color="#409EFF" style="border: none;">

        <el-menu-item index="/welcome/dashboard">
          <i class="el-icon-s-data"></i>
          <span slot="title"></span>
        </el-menu-item>

        <!--  -->
        <el-menu-item index="/welcome/sales">
          <i class="el-icon-shopping-cart-2"></i>
          <span slot="title"></span>
        </el-menu-item>

        <el-menu-item index="/welcome/products">
          <i class="el-icon-box"></i>
          <span slot="title"></span>
        </el-menu-item>

        <el-menu-item index="/welcome/documents">
          <i class="el-icon-document-copy"></i>
          <span slot="title"></span>
        </el-menu-item>

        <el-menu-item index="/welcome/locations">
          <i class="el-icon-location-outline"></i>
          <span slot="title"></span>
        </el-menu-item>

        <!--   -->
        <el-menu-item index="/welcome/logs">
          <i class="el-icon-notebook-2"></i>
          <span slot="title"></span>
        </el-menu-item>

        <el-menu-item index="/welcome/users" v-if="isAdmin">
          <i class="el-icon-user"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <!--  Header -->
      <el-header
        style="background: #fff; box-shadow: 0 1px 4px rgba(0,21,41,.08); display: flex; justify-content: space-between; align-items: center; height: 60px;">
        <div class="header-left">
          <span class="breadcrumb-text">{{ $route.meta.title }}</span>
        </div>
        <div class="header-right">
          <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link" style="cursor: pointer; color: #333;">
              <i class="el-icon-user-solid"></i> {{ username }} <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="logout" icon="el-icon-switch-button"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>

      <!--  () -->
      <el-main style="background-color: #f0f2f5; padding: 20px;">
        <!--  Dashboard, ProductManager  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'WelcomeLayout',
  data() {
    return {
      // ID
      roleId: sessionStorage.getItem('roleId'),
      username: sessionStorage.getItem('username')
    }
  },
  computed: {
    // 
    isAdmin() {
      return this.roleId === '1';
    }
  },
  methods: {
    handleCommand(command) {
      if (command === 'logout') {
        sessionStorage.clear();
        this.$message.success('');
        this.$router.push('/login');
      }
    }
  }
}
</script>

<style scoped>
.sys-logo {
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  background-color: #002140;
}

.breadcrumb-text {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
}
</style>