<template>
  <div class="app-container">
    <el-card class="box-card" shadow="never">
      <!--  -->
      <div slot="header" style="display: flex; justify-content: space-between;">
        <div>
            <el-input v-model="search" placeholder="" size="small" style="width: 200px; margin-right: 10px;" clearable></el-input>
        </div>
        <div>
            <el-button type="primary" icon="el-icon-plus" size="small" @click="handleAdd" v-if="isAdmin"></el-button>
            <el-button icon="el-icon-refresh" size="small" @click="fetchData"></el-button>
        </div>
      </div>
      
      <!--  -->
      <el-table :data="filteredData" v-loading="loading" border style="width: 100%">
        <el-table-column prop="proId" label="ID" width="60" align="center"></el-table-column>
        <el-table-column prop="proName" label="" min-width="120"></el-table-column>
        <el-table-column prop="proCategory" label="" width="100" align="center">
          <template slot-scope="scope">
            <el-tag size="small" type="info">{{ scope.row.proCategory }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="proPrice" label="" width="100" sortable align="right">
            <template slot-scope="scope"> {{ scope.row.proPrice }}</template>
        </el-table-column>
        
        <el-table-column prop="proNum" label="" width="120" sortable align="center">
             <template slot-scope="scope">
                <div v-if="scope.row.proNum <= 10" style="color: red; font-weight: bold;">
                    <i class="el-icon-warning"></i> {{ scope.row.proNum }}
                </div>
                <div v-else style="color: #67C23A; font-weight: bold;">
                    {{ scope.row.proNum }}
                </div>
             </template>
        </el-table-column>
        
        <el-table-column label="" width="180" align="center">
            <template slot-scope="scope">
                <!--   -->
                <el-button-group>
                    <el-button size="mini" type="success" plain @click="openStockDialog(scope.row, '')"></el-button>
                    <el-button size="mini" type="warning" plain @click="openStockDialog(scope.row, '')"></el-button>
                </el-button-group>
            </template>
        </el-table-column>

        <el-table-column label="" width="120" align="center" v-if="isAdmin">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" circle @click="handleEdit(scope.row)"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" circle @click="handleDelete(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- /  () -->
    <el-dialog :title="form.proId ? '' : ''" :visible.sync="dialogVisible" width="500px" :close-on-click-modal="false" v-if="isAdmin">
      <el-form :model="form" label-width="80px">
        <el-form-item label=""><el-input v-model="form.proName"></el-input></el-form-item>
        <el-form-item label="">
          <el-select v-model="form.proCategory" style="width: 100%"><el-option value="" label=""></el-option><el-option value="" label=""></el-option><el-option value="" label=""></el-option></el-select>
        </el-form-item>
        <el-form-item label=""><el-input-number v-model="form.proPrice" :precision="2" :step="0.1"></el-input-number></el-form-item>
        <el-form-item label=""><el-input-number v-model="form.proNum" :min="0" :disabled="!!form.proId"></el-input-number></el-form-item>
        <el-form-item label=""><el-input type="textarea" v-model="form.proDesc"></el-input></el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="submitForm"></el-button>
      </span>
    </el-dialog>

    <!--    -->
    <el-dialog :title="stockForm.type + ''" :visible.sync="stockDialogVisible" width="400px">
        <el-form :model="stockForm" label-width="80px">
            <el-form-item label="">
                <!--  -->
                <el-input v-model="stockForm.proName" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-tag>{{ stockForm.currentNum }}</el-tag>
            </el-form-item>
            <el-form-item :label="stockForm.type + ''">
                <el-input-number v-model="stockForm.docCount" :min="1" style="width: 100%"></el-input-number>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="stockForm.handler" disabled></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer">
            <el-button @click="stockDialogVisible = false"></el-button>
            <el-button type="primary" @click="submitStock"></el-button>
        </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: 'ProductManagerPage',
  data() {
    return {
      loading: false,
      tableData: [],
      search: '',
      // 
      dialogVisible: false,
      form: { proId: null, proName: '', proCategory: '', proPrice: 0, proNum: 0, proDesc: '' },
      
      //  
      stockDialogVisible: false,
      stockForm: {
          proId: null,
          proName: '',
          currentNum: 0,
          type: '',
          docCount: 1,
          handler: ''
      },
      roleId: sessionStorage.getItem('roleId') || '2'
    };
  },
  computed: {
      filteredData() {
          if (!this.search) return this.tableData;
          return this.tableData.filter(data => 
            !this.search || data.proName.toLowerCase().includes(this.search.toLowerCase())
          )
      },
      isAdmin() {
        return this.roleId === '1';
      }
  },
  created() {
    this.fetchData();
    // 
    this.$root.$on('documentApproved', () => {
      this.fetchData();
    });
  },
  beforeDestroy() {
    // 
    this.$root.$off('documentApproved');
  },
  methods: {
    async fetchData() {
      this.loading = true;
      try {
        const res = await this.$xhr.get('/product/list');
        if (res.code === 0) {
          this.tableData = res.data;
        }
      } catch (e) { console.error(e); } 
      finally { this.loading = false; }
    },
    
    // /
    handleAdd() {
      this.form = { proId: null, proName: '', proCategory: '', proPrice: 0, proNum: 0, proDesc: '' };
      this.dialogVisible = true;
    },
    handleEdit(row) {
      this.form = { ...row };
      this.dialogVisible = true;
    },
    async handleDelete(row) {
      try {
        await this.$confirm('?', '', { type: 'warning' });
        await this.$xhr.post('/product/delete', row);
        this.$message.success('');
        this.fetchData();
      } catch (e) { /* cancel */ }
    },
    async submitForm() {
      const url = this.form.proId ? '/product/update' : '/product/add';
      try {
        await this.$xhr.post(url, this.form);
        this.$message.success('');
        this.dialogVisible = false;
        this.fetchData();
      } catch (e) { console.error(e) }
    },

    //  
    openStockDialog(row, type) {
        this.stockForm = {
            proId: row.proId,
            proName: row.proName,
            currentNum: row.proNum,
            type: type,
            docCount: 1,
            handler: sessionStorage.getItem('username') || ''
        };
        this.stockDialogVisible = true;
    },

    //  
    async submitStock() {
            //  /document/add 
            const docData = {
                docType: this.stockForm.type === '' ? '' : '', // 
                proId: this.stockForm.proId,    // ID
                docCount: this.stockForm.docCount,    // 
                docHandler: this.stockForm.handler, // 
                docStatus: '',            // 
                picIds: []                      // 
            };

            try {
                const res = await this.$xhr.post('/document/add', docData);
                if(res.code === 0) {
                    this.$message.success(`${this.stockForm.type}`);
                    this.stockDialogVisible = false;
                    // 
                } else {
                    this.$message.error(res.msg);
                }
            } catch(e) {
                console.error(e);
            }
    }
  }
};
</script>