<template>
  <div class="app-container">
    <el-card shadow="never">
      <div slot="header" class="clearfix">
        <el-button type="primary" icon="el-icon-plus" @click="handleAdd"></el-button>
        <el-button icon="el-icon-refresh" @click="fetchData"></el-button>
      </div>

      <el-table :data="tableData" v-loading="loading" border style="width: 100%">
        <el-table-column prop="locationCode" label="" width="150" sortable></el-table-column>
        <el-table-column prop="product" label=""></el-table-column>
        <el-table-column prop="locationStatus" label="" width="120">
            <template slot-scope="scope">
                <el-tag :type="scope.row.locationStatus === '' ? 'success' : 'danger'">
                    {{ scope.row.locationStatus }}
                </el-tag>
            </template>
        </el-table-column>
        <el-table-column prop="locationAttributes" label="(:/)"></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)" v-if="isAdmin"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row)" v-if="isAdmin"></el-button>
            <span v-if="!isAdmin"></span>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog :title="form.isEdit ? '' : ''" :visible.sync="dialogVisible" width="500px" v-if="isAdmin">
      <el-form :model="form" label-width="100px">
        <el-form-item label="">
          <el-input v-model="form.locationCode" :disabled="form.isEdit"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select 
            v-model="form.product" 
            placeholder="" 
            style="width: 100%" 
            filterable
            multiple
            clearable>
            <el-option
              v-for="item in productList"
              :key="item.proId"
              :label="item.proName"
              :value="item.proName">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
           <el-select v-model="form.locationStatus" style="width:100%">
               <el-option value="" label=""></el-option>
               <el-option value="" label=""></el-option>
           </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="form.locationAttributes" placeholder="A / "></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'LocationManagerPage', //  ESLint 
  data() {
    return {
      loading: false,
      tableData: [],
      productList: [], // 
      dialogVisible: false,
      form: { locationCode: '', product: [], locationStatus: '', locationAttributes: '', isEdit: false },
      roleId: sessionStorage.getItem('roleId') || '2'
    };
  },
  computed: {
    isAdmin() {
      return this.roleId === '1';
    }
  },
  created() {
    this.fetchData();
    this.fetchProducts(); // 
  },
  methods: {
    async fetchData() {
      this.loading = true;
      try {
        const res = await this.$xhr.get('/location/all');
        if (res.code === 0) {
          this.tableData = res.data;
        }
      } catch (e) { console.error(e) } 
      finally { this.loading = false; }
    },
    async fetchProducts() {
      try {
        const res = await this.$xhr.get('/product/list');
        if (res.code === 0) {
          this.productList = res.data;
        }
      } catch (e) { 
        console.error(e);
      }
    },
    handleAdd() {
      // 
      this.form = { locationCode: '', product: [], locationStatus: '', locationAttributes: '', isEdit: false };
      this.dialogVisible = true;
    },
    handleEdit(row) {
      // 
      if (!this.isAdmin) {
        this.$message.warning('');
        return;
      }
      
      // 
      let productArray = [];
      if (row.product) {
        if (Array.isArray(row.product)) {
          productArray = row.product;
        } else if (typeof row.product === 'string') {
          // 
          productArray = row.product.split(',').map(item => item.trim()).filter(item => item);
        } else {
          productArray = [row.product];
        }
      }
      
      this.form = { 
        ...row, 
        product: productArray,
        isEdit: true 
      };
      this.dialogVisible = true;
    },
    async handleDelete(row) {
      // 
      if (!this.isAdmin) {
        this.$message.warning('');
        return;
      }
      try {
        await this.$confirm('?', '', { type: 'warning' });
        //  POST 
        await this.$xhr.post('/location/deleteLocation', row);
        this.$message.success('');
        this.fetchData();
      } catch (e) { /* cancel */ }
    },
    async submitForm() {
      // 
      if (!this.isAdmin) {
        this.$message.warning('');
        return;
      }
      
      //  isEdit 
      const submitData = { ...this.form };
      delete submitData.isEdit;
      
      // 
      if (submitData.product && Array.isArray(submitData.product) && submitData.product.length === 0) {
        // null
        submitData.product = '';
      } 
      // 
      else if (submitData.product && Array.isArray(submitData.product) && submitData.product.length === 1) {
        submitData.product = submitData.product[0]; 
      }
      // 
      else if (submitData.product && Array.isArray(submitData.product)) {
        submitData.product = submitData.product.join(',');
      }
      
      const url = this.form.isEdit ? '/location/updateLocation' : '/location/addLocation';
      try {
        await this.$xhr.post(url, submitData);
        this.$message.success('');
        this.dialogVisible = false;
        this.fetchData();
      } catch (e) { 
        console.error(e);
        this.$message.error('' + (e.message || ''));
      }
    }
  }
};
</script>